import UniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import UniBackground from '@/components/uni-background/uni-background.vue';
export default {
	components:{
		UniNavBar,
		UniBackground
	},
	data() {
		return {
			scroll:0,
			editing:false,
			navButtons:{
				back:{
					type:'normal',
					text:'取消'
				},
				setting:{
					type:'normal',
					text:'handle',
					classify:'setting'
				}
			},
			imageLib:{
				checked:'../../static/bg/check.png',
				check:'../../static/bg/checkbox.png',
				more:'../../static/icons/more.png'
			},
			payMethods:[
				{
					id:1,
					name:'微信',
					status:'canUse',
					checked:1,
					icon:'../../static/icons/icon_wechatpay_light.png'
				},
				{
					id:2,
					name:'支付宝',
					status:'canUse',
					checked:0,
					icon:'../../static/icons/icon_alipay_light.png'
				},
				{
					id:3,
					name:'银行卡',
					status:'noUse',
					checked:0,
					icon:'../../static/icons/icon_bankcard.png'
				},
			]
		};
	},
	onPageScroll(val){
		this.scroll = val.scrollTop;
	},
	methods:{
		setChecked(id){
			this.payMethods.map(item=>{
				item.checked = 0;
			});
			this.payMethods[id].checked = 1;
		},
		handleButtons(res){
			if(res == 'setting'){
				this.editing = this.editing?false:true;
			}
		},
		editPay(name){
			uni.navigateTo({
				url:'../editpaymethod/editpaymethod?name='+name
			})
		}
	}
}
